.stars {
  display: flex;
  justify-content: space-between;

  &__star {
    background: url("../images/star.svg") no-repeat center;
    width: 16px;
    height: 16px;

    &:not(:last-child) {
      margin-right: 4px;
    }

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4) {
      background-image: url("../images/star-active.svg");
    }
  }
}
